#home {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  justify-items: stretch;
  min-height: 100vh;
  background-image: linear-gradient(
      rgba(6, 44, 48, 0.53),
      rgba(6, 44, 48, 0.53)
    ),
    url("../../public/images/Home_background.png");
  background-size: cover;
}

.hero_image {
  height: clamp(600px, 50vw, 95vh);
  position: absolute;
  bottom: 0;
  z-index: 2;
}

.home_content {
  height: 100%;
  display: flex;
  justify-content: flex-end;
  flex: 1;
}
.home_left {
  width: 50%;
}

.home_right {
  display: flex;
  flex-direction: column;
  justify-items: stretch;
  width: 50%;
}
.home_bottom {
  min-height: 170px;
  background-image: linear-gradient(rgba(6, 44, 48, 0), rgba(6, 44, 48, 0)),
    url("../../public/home_Vector.svg");
  background-size: cover;
  width: 100%;
}
.home_content_header {
  display: flex;
  align-items: center;

  flex: 1;
}
.home_content_header > h1 {
  font-size: clamp(3rem, 6.5vw, 6rem);
  width: 300px;
  font-weight: 500;
  z-index: 5;
}
.logos {
  display: flex;
  /* justify-content: center; */
  padding-bottom: 15px;
}
.home_logos {
  z-index: 20;
  /* width: clamp(200px, 80%, 300px); */
  min-width: 200px;
  max-width: 300px;
}
.home_icon {
  /* color: rgba(255, 255, 255, 0.7); */
  color: white;
  font-size: 37.5px;
  /* padding: 0 15px; */
}
.home_icon:hover {
  color: rgba(253, 190, 106, 1);
}

@media (max-width: 1090px) {
  .home_right {
    align-items: center;
  }
}
@media (max-width: 895px) {
  .hero_image {
    right: 35vw;
  }
}
@media (max-width: 620px) {
  .hero_image {
    display: none;
  }
  .home_content {
    justify-content: center;
  }
  .logos {
    min-width: 300px;
  }
}
